<template>
  <div class="channel_container">
    <van-cell :border="false">
      <div class="channel_title" slot="title">我的频道</div>
      <van-button
        class="my_edit"
        slot="right-icon"
        type="danger"
        size="mini"
        plain
        round
        @click="isEdit = !isEdit"
        >{{ isEdit ? `完成` : `编辑` }}</van-button
      >
    </van-cell>
    <van-grid :gutter="10" direction="horizontal">
      <van-grid-item
        v-for="(item, index) in channelList"
        :key="item.id"
        :text="item.name"
        :class="{
          grid_item: true,
          active: index == activeIndex,
        }"
        @click="checkChennel(index)"
      >
        <van-icon
          slot="icon"
          name="clear"
          v-if="index !== 0 && isEdit"
          @click="onUserChennelEdit(item.id, index)"
        />
      </van-grid-item>
    </van-grid>

    <van-cell :border="false">
      <div class="channel_title" slot="title">频道推荐</div>
    </van-cell>

    <van-grid :gutter="10">
      <van-grid-item
        class="grid_item"
        v-for="item in recomnedChannels"
        :key="item.id"
        :text="item.name"
        @click="addChannel(item)"
      />
    </van-grid>
  </div>
</template>

<script>
import {
  getAllChannels,
  editUserChannels,
  deleteUserChannels,
} from '@/api/home.js';
import { mapState } from 'vuex';
import { setItem } from '@/utils/storage.js';

export default {
  name: 'ChannelEdit',
  data() {
    return {
      allChannels: [], // 所有频道
      isEdit: false, // 是否编辑
    };
  },
  props: {
    channelList: {
      type: Array,
      required: true,
    },
    activeIndex: {
      type: Number,
      required: true,
    },
  },
  computed: {
    // vuex中用户token
    ...mapState(['userToken']),
    recomnedChannels() {
      const arr = [];
      this.allChannels.forEach((item) => {
        let flag = false;

        for (let i = 0; i < this.channelList.length; i++) {
          if (item.id === this.channelList[i].id) {
            flag = true;
            break;
          }
        }
        if (!flag) {
          arr.push(item);
        }
      });
      return arr;
    },
  },
  methods: {
    // 获取所有的频道
    async reqAllChannels() {
      try {
        const { data } = await getAllChannels();
        const { channels } = data.data;

        this.allChannels = channels;
      } catch (e) {
        console.log(e);
      }
    },
    // 添加到我的频道
    async addChannel(channel) {
      this.channelList.push(channel);
      // 用户是否登录决定我的频道存储位置
      if (this.userToken) {
        // 已经登录
        try {
          await editUserChannels(id);
        } catch (e) {
          console.log(e);
        }
      } else {
        // 未登录
        setItem('user_chennels', this.channelList);
      }
    },
    // 编辑删除我的频道
    async onUserChennelEdit(id, index) {
      this.channelList.splice(index, 1);
      if (index <= this.activeIndex) {
        this.$emit('deleteChannel', this.activeIndex - 1);
      }
      if (this.userToken) {
        // 已经登录
        try {
          const res = await deleteUserChannels(id);
          console.log(res);
        } catch (e) {
          console.log(e);
        }
      } else {
        // 未登录
        setItem('user_chennels', this.channelList);
      }
    },
    // 点击频道实现切换
    checkChennel(index) {
      if (this.isEdit) {
        this.$emit('changeChannel', index);
      }
    },
  },
  created() {
    this.reqAllChannels();
  },
};
</script>

<style lang="less" scoped>
.channel_container {
  padding-top: 54px;
  .channel_title {
    font-size: 16px;
    color: #333;
  }
  .grid_item {
    width: 80px;
    height: 43px;
    /deep/ .van-grid-item__content {
      text-align: center;
      background-color: #f4f5f6;
      .van-grid-item__text {
        font-size: 14px;
        color: #222;
      }
    }
    &.active {
      /deep/ .van-grid-item__text {
        color: rgb(196, 13, 150);
      }
    }
    /deep/.van-grid-item__icon-wrapper {
      z-index: 10;
      position: absolute;
      top: -15px;
      right: -6px;
      .van-icon {
        font-size: 16px;
        color: #255;
      }
    }
  }
  .my_edit {
    padding: 0 10px;
  }
}
</style>
